.a11yHidden {
  overflow: hidden;
  position: absolute;
  clip: rect(0 0 0 0);
  clip-path: polygon(0 0, 0 0, 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  white-space: nowrap;
}

.resetBoxModel {
  margin: 0;
  border: 0;
  padding: 0;
}

.resetList {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

.divider {
  display: block;
  height: 1px;
  width: 100%;
  background-color: #cccccc;
}